@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #f2f2f2;
  font-family: "Poppins", sans-serif;
}
.flex {
  display: flex;
}
.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between;
}
.items-center {
  align-items: center;
}
.main {
  background: rgb(219, 140, 255);

  width: fit-content;
  margin: 10vh auto;
  border-radius: 8px;
  padding: 20px;
}
.main h1 {
  text-align: center;
  color: #f2f2f2;
  margin-bottom: 20px;
}
form {
  margin-bottom: 20px;
  padding: 0;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}
form input[type="text"] {
  font-size: 18px;
  padding: 8px 10px;
  border: 0;
  outline: 0;
  border-radius: 4px 0 0 4px;
  color: #333;
}
form button {
  border-radius: 0 4px 4px 0;
  border: 0;
  padding: 0 20px;
  cursor: pointer;
  font-size: 14px;
  background-color: rgb(188, 72, 255);
  color: white;
}

.main h3 {
  color: white;
  font-size: 18px;
  font-weight: 100;
  padding: 6px 0;
}
.main hr {
  border: 1px solid #f2f2f2;
}
.todos-list {
  margin-top: 20px;
  max-height: 50vh;
  overflow: auto;
  padding-right: 10px;
}
.todos-list div {
  background-color: white;
  margin-bottom: 12px;
  padding: 8px 10px;
  border-radius: 4px;
}
.todo-text {
  text-transform: capitalize;
  margin-left: 6px;
  transition: color 0.4s ease;
}

.todos-list div i {
  border: 0;
  border-radius: 50%;
  font-weight: bolder;
  font-size: 20px;
  color: violet;
  cursor: pointer;
  text-align: center;
}
.todos-list::-webkit-scrollbar {
  border-radius: 20px;
}
.todos-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(15px);
  border-radius: 20px;
}
.todos-list::-webkit-scrollbar-thumb {
  background: #f2f2f2;
  border-radius: 20px;
}
.todos-list div .todo-item {
  position: relative;
}
.completed {
  color: rgb(212, 212, 212);
}
